#file-zone {
  background-color: #f1f0f0;
  -ms-flex-positive: 1;
      flex-grow: 1;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
  padding: 5px;
}

#file {
  width: 600px;
  -ms-flex-positive: 1;
      flex-grow: 1;
  background-color: #fff;
  border: 1px solid #e4dede;
}

.text {
  padding: 15px;
}

.text > .textItem {
  -webkit-transition: 0.3s color ease;
  -o-transition: 0.3s color ease;
  transition: 0.3s color ease;
}

.text > .textItem:hover {
  color: red;
}

.bold {
  font-weight: bold;
}

.italic {
  font-style: italic;
}

.underline {
  text-decoration: underline;
}



